<template>
  <div>
    <!-- 标签使用原则:
        规则:先定义,后使用
        语法:el-breadcrumb 找到名称为el-Breadcrumb组件进行展示
        定义elementUI组件:按需导入组件
          从elementUI中导入特定的组件
          1.import {breadcrumb} from 'element-ui'
          2.Vue.use
     -->

    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <div class="text item">
        <!-- 栅格:每行24格,可以动态的缩放 -->
        <el-row :gutter="15">
          <el-col :span="8">
            <el-input placeholder="请输入内容" class="input-with-select">
              <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
          </el-col>
          <el-col :span="3">
            <el-button type="primary">添加用户</el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="16">
            <el-table :data="tableData" style="width: 100%" border stripe>
              <el-table-column prop="id" label="序号" width="60px"></el-table-column>
              <el-table-column prop="name" label="用户名"></el-table-column>
              <el-table-column prop="phone" label="电话"></el-table-column>
              <el-table-column prop="email" label="邮箱"></el-table-column>
              <el-table-column prop="status" label="状态"></el-table-column>
              <el-table-column prop="right" label="操作"></el-table-column>
            </el-table>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="16">
            <span class="demonstration"/>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="5"
              :page-sizes="[5, 10, 15, 20]"
              :page-size="10"
              layout="total, sizes, prev, pager, next, jumper"
              :total="100"
            ></el-pagination>
          </el-col>
        </el-row>
      </div>
    </el-card>
    <div>
      <el-upload
        class="upload-demo"
        action="https://jsonplaceholder.typicode.com/posts/"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :file-list="fileList"
        list-type="picture"
        drag
        multiple>
        <el-button size="small" type="primary">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>
    </div>
  </div>
</template>

<script>
// 对外声明组件属性/方法等参数,要被根组件调用
export default {
  data() {
    return {
      msg: '我是VUE子组件',
      tableData: [{id: 100, name: '老王', phone: '11111'}, {id: 101, name: '马钊', phone: '110'}, {
        id: 101,
        name: '老历',
        phone: '12222'
      }],
      fileList: [{
        name: 'food.jpeg',
        url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
      }, {
        name: 'food2.jpeg',
        url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
      }]
    }
  },
  methods: {
    handleSizeChange(size) {
      console.log('当前条数' + size)
      alert('当前条数' + size)
    },
    handleCurrentChange(current) {
      alert('当前页数' + current)
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    }
  }
}
</script>

<style lang="less" scoped></style>
